
import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import { toast } from "react-toastify";
import { Nav, Tab } from 'react-bootstrap';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import PerfectScrollbar from 'react-perfect-scrollbar'
import PreviewOnsite from './components/PreviewOnsite';
import UploadImage from './components/UploadImage';
import UploadLogo from './components/UploadLogo';
import SubmitFeedRequest from '../Elements/SubmitFeedRequest';
import { getOnsiteSettings, updateOnsiteFeed } from '../../../../Services/Actions/addFeedAction';
import { mailSendRequest } from '../../../../Services/Actions/mailActions'
import { getFeedsData } from '../../../../Services/Actions/filterActions';
import LinkOnsite from './components/LinkOnsite';
import UrlInput from '../../CommonComponents/UrlInput';
import WebsiteOnsite from './components/WebsiteOnsite';
import SMSMarketingOnsite from './components/SMSMarketingOnsite';
const ColorPicker = React.lazy(() => import('../../CommonComponents/ColorPicker'));
const ShareEmbedModal = React.lazy(() => import('../../../Main/PublishCommonComponents/ShareEmbedModal'));

class OnsiteUpload extends Component {
    state = {
        onSiteEnable: false,
        loading: false,
        title: 'Submit your post',
        subTitle: 'You can share your reviews/rating here',
        error: false,
        subtitleError: false,
        selectedColor: '#eff2f3',
        btnText: 'Submit',
        btnTextError: false,
        msgError: false,
        backgroundImage: '',
        msg: 'The post(s) will be appeared after the admin approval',
        tinyUrl: "",
        uniqueCode: null,
        isLoading: false,
        showModal: false,
        dataShare: null,
        logoImage: null,
        buttonText: "Create Connection"
    }

    componentWillMount() {
        this.onLoadSettings()
    }
    onLoadSettings = () => {
        getOnsiteSettings().then(response => {
            const { response_code, responseData } = response.data;

            if (response_code == 200) {
                const { feeds } = responseData;

                if (feeds && feeds.length > 0) {
                    const { onsite_settings, collection, value4, value1 } = feeds[0];
                    const settings = JSON.parse(onsite_settings);
                    if (settings && Object.keys(settings).length > 0) {
                        const { onsite_bg_color_wg, onsite_bg_image_wg, onsite_popup_btn_txt, onsite_popup_sub_heading, onsite_popup_title, onsite_succ_msg, onsite_status, onsite_logo } = settings;
                        this.props.onUGCUploadCollection(collection)
                        this.setState({ title: onsite_popup_title, subTitle: onsite_popup_sub_heading, btnText: onsite_popup_btn_txt, msg: onsite_succ_msg, backgroundImage: onsite_bg_image_wg, logoImage: onsite_logo, selectedColor: onsite_bg_color_wg, onSiteEnable: onsite_status == 1 ? true : false, tinyUrl: value4, uniqueCode: value1 && String(value1).length > 0 ? `https://cloud.taggbox.com/onsiteupload/images/${value1}.png` : null, buttonText: "Update Connection" })
                    }

                }
            }
        }).catch(ex => {

        })
    }

    onChangeHandler = event => {
        this.setState({ [event.target.name]: event.target.value, error: false, subtitleError: false, subtitleError: false, btnTextError: false, msgError: false });
    }

    onCheckHandler = event => {
        this.setState({ [event.target.name]: event.target.checked });
    }

    onColorChange = (value) => {
        this.setState({ selectedColor: value })
    }

    onClickTouploadImage = (value, callBack) => {
        this.setState({ backgroundImage: value }, () => callBack())
    }
    onClickTouploadLogo = (value, callBack) => {
        this.setState({ logoImage: value }, () => callBack())
    }

    onSubmit = () => {
        const { title, subTitle, backgroundImage, selectedColor, onSiteEnable, btnText, msg, logoImage } = this.state;
        const { collection } = this.props
        var collectionId = []
        if (collection && collection.length > 0) {
            collection.map((iCollection) => {
                collectionId.push(iCollection.value)
            })
        }
        const isCollection = collectionId && collectionId.length > 0 ? { "collection": collectionId.join(",") } : { "collection": "" }
        if (title == '') {
            this.setState({ error: true });
        } else if (subTitle == '') {
            this.setState({ subTitle: true });
        } else if (btnText == '') {
            this.setState({ btnTextError: true });
        } else if (msg == '') {
            this.setState({ msg: true });
        } else {
            const obj = {
                ...isCollection,
                network: 29,
                filter: 2,
                moderation: 0,
                onsite_qr_code_btn_text: "Scan QR code or click here to submit reviews/rating",
                onsite_btn_color: "#000000",
                onsite_btn_txt_color: "#ffffff",
                onsite_link_font_btn: "Lato",
                onsite_css_font_btn: "Lato",
                onsite_font_varient_btn: "",
                btn_fontSize: 16,
                onsite_qr_code_status: 1,
                onsite_popup_title: title,
                onsite_popup_btn_txt: btnText,
                onsite_popup_sub_heading: subTitle,
                onsite_term_url: "https://taggbox.com/on-site-uploads-content-rights/",
                onsiteImgBack: "",
                onsite_logo: logoImage,//"https://cloud.taggbox.com/widget/themes/media/images/taggbox.svg",
                Onsite_logoImageValue: "",
                onsite_bg_color_wg: selectedColor,
                onsiteBackImage: "",
                onsite_bg_image_wg: backgroundImage,
                Onsite_backImageValue: "",
                onsite_succ_msg: msg,
                onsite_thank_txt_color: "#000000",
                onsite_link_font_msg: "Default",
                onsite_css_font_msg: "Default",
                onsite_font_varient_msg: "",
                onsite_thank_fontSize: 16,
                onsite_status: onSiteEnable ? 1 : 0
            };
            this.setState({ loading: true, error: false, subtitleError: false, subtitleError: false, btnTextError: false, msgError: false });
            updateOnsiteFeed(obj, (getFeedId) => {
                this.props.getAllfeeds()
                this.onLoadSettings()
                this.setState({ loading: false });
                //this.props.history(`/assets?feedId=${getFeedId}`);
                //this.props.editMediaHandleCallback(false);
            });
        }

    }
    shareEmbedCallback = (shareEmbedEvent) => {
        this.setState({
            showModal: shareEmbedEvent,
            dataShare: null
        })
    }

    onClickToShare = (mailId) => event => {
        var self = this;
        const { dataShare } = this.state
        this.setState({ isLoading: true })
        if (mailId && String(mailId).length > 0) {
            mailSendRequest({ htmlCode: dataShare, email: mailId, type: 4 }).then((response) => {
                toast.success("UGC Uploader URL successfully shared!")
                self.setState({ showModal: false, isLoading: false })
            })
        }
        else {
            toast.error("Please enter email id.")
        }
    }

    onClickToOpen = (dataShare) => event => this.setState({
        showModal: true,
        dataShare
    })
    render() {
        const { title, subTitle, error, onSiteEnable, subtitleError, backgroundImage, loading, selectedColor, btnText, msg, btnTextError, msgError, showModal, logoImage, tinyUrl } = this.state;
        return <>   <Tab.Container id="ugc-upload" defaultActiveKey="tab_uploader">
            <Nav variant="pills" className="d-flex flex-row mb-3 light-pills">
                <Nav.Item>
                    <Nav.Link className="me-2" eventKey="tab_uploader">Uploader</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link className="me-2" eventKey="tab_publish">Publish Options</Nav.Link>
                </Nav.Item>
            </Nav>
            <Tab.Content>
                <Tab.Pane fade={true} eventKey="tab_uploader">
                    <Tab.Container id="ugc-upload2" defaultActiveKey="tab_uploader1">
                        <Nav variant="underline" className="d-flex flex-row border-bottom mb-3">
                            <Nav.Item>
                                <Nav.Link className="me-3 me-lg-6 px-0 fw-medium fs-8" eventKey="tab_uploader1">
                                    <SVG className="me-1" src={baseUrl('media/icons/svg-icons/ugc-uploader-line.svg')} height={16} width={16} />
                                    Uploader Options
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                        <Tab.Content>
                            <Tab.Pane fade={true} eventKey="tab_uploader1">
                                <div className="d-flex align-items-start flex-column flex-md-row">
                                    <div className="uploadInnerAside flex-shrink-0 mb-3 mb-sm-0">
                                        <label className="form-check form-switch py-2 align-items-center justify-content-between mb-2 h-40px py-1">
                                            <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800 me-2">Enable</span>
                                            <input className="form-check-input" type="checkbox" name="onSiteEnable" checked={onSiteEnable} onChange={(e) => this.onCheckHandler(e)} />
                                        </label>
                                        <PerfectScrollbar>
                                            <div className="upload_aside_scroll">
                                                <div className="mb-2 mt-2">
                                                    <label className="mb-1 fs-9 fw-medium">Title</label>
                                                    <div className="">
                                                        <input type="text" name="title" className={`form-control form-control-sm ${error ? 'is-invalid' : ''}`} placeholder="Enter Sub Title..." value={title} onChange={(e) => this.onChangeHandler(e)} />
                                                        {error ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                <div className="mb-2">
                                                    <label className="mb-1 fs-9 fw-medium">Sub Heading</label>
                                                    <div className="">
                                                        <textarea name="subTitle" className={`form-control form-control-sm textarea-min-row-2 ${subtitleError ? 'is-invalid' : ''}`} placeholder="Enter Title..." value={subTitle} onChange={(e) => this.onChangeHandler(e)} />
                                                        {subtitleError ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                <UploadLogo imageStyle={{ paddingBottom: '50%' }} logoImage={logoImage} onClickTouploadLogo={this.onClickTouploadLogo} />
                                                <label className="d-flex align-items-center justify-content-between h-40px py-1 position-relative">
                                                    <span className="ms-0 fs-9 fw-medium">Background Color</span>
                                                    <Suspense fallback={<></>}>
                                                        <ColorPicker color={selectedColor} onChangeColor={this.onColorChange} />
                                                    </Suspense>
                                                </label>
                                                <UploadImage imageStyle={{ paddingBottom: '66%' }} backgroundImage={backgroundImage} onClickTouploadImage={this.onClickTouploadImage} />
                                                <div className="mb-2">
                                                    <label className="mb-1 fs-9 fw-medium">Submit Button Text</label>
                                                    <div className="">
                                                        <input type="text" name="btnText" className={`form-control form-control-sm ${btnTextError ? 'is-invalid' : ''}`} placeholder="Enter Button Text..." value={btnText} onChange={(e) => this.onChangeHandler(e)} />
                                                        {btnTextError ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                                <div className="mb-2">
                                                    <label className="mb-1 fs-9 fw-medium">Thank You Message</label>
                                                    <div className="w-100 pb-2">
                                                        <textarea className={`form-control form-control-sm textarea-min-row-4 ${msgError ? 'is-invalid' : ''}`} name="msg" row={5} value={msg} onChange={(e) => this.onChangeHandler(e)}  >
                                                        </textarea>
                                                        {msgError ? <span className="invalid-feedback">This field is required</span> : null}
                                                    </div>
                                                </div>
                                            </div>
                                        </PerfectScrollbar>
                                    </div>
                                    <div className="d-flex flex-grow-1 w-100 flex-column bg-light p-xl-5 p-3 mb-3 rounded-2">
                                        <p className="text-start mb-3"><strong>Uploader URL</strong></p>
                                        <div className="mb-2">
                                            <UrlInput inputUrl={tinyUrl} inputId={'onsite4'} inputSize={''} onClickToOpen={this.onClickToOpen}
                                                share={[{ status: true, tooltip: 'Share Onsite Url' }]}
                                                copy={[{ status: true, tooltip: 'Share Onsite Url' }]}
                                                edit={[{ status: true, tooltip: 'Share Onsite Url' }]} />
                                        </div>
                                        <PreviewOnsite {...this.state} />
                                    </div>
                                </div>
                            </Tab.Pane>
                        </Tab.Content>
                    </Tab.Container>
                </Tab.Pane>
                <Tab.Pane fade={true} eventKey="tab_publish">
                    <Tab.Container id="tab_publish_in" defaultActiveKey="tab_link">
                        <Nav variant="underline" className="d-flex flex-row border-bottom mb-3">
                            <Nav.Item>
                                <Nav.Link className="me-3 me-lg-6 px-0 fw-medium fs-8" eventKey="tab_link">
                                    <i className="ri-links-fill me-1"></i>
                                    Link
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link className="me-3 me-lg-6 px-0 fw-medium fs-8" eventKey="tab_website">
                                    <i className="ri-window-line me-1"></i>
                                    Website
                                </Nav.Link>
                            </Nav.Item>
                            <Nav.Item>
                                <Nav.Link className="me-3 me-lg-6 px-0 fw-medium fs-8" eventKey="tab_sms">
                                    <i className="ri-message-line me-1"></i>
                                    SMS Marketing
                                </Nav.Link>
                            </Nav.Item>
                        </Nav>
                        <Tab.Content>
                            <Tab.Pane fade={true} eventKey="tab_link">
                                <LinkOnsite {...this.state} onClickToOpen={this.onClickToOpen} />
                            </Tab.Pane>
                            <Tab.Pane fade={true} eventKey="tab_website">
                                <WebsiteOnsite {...this.state} onClickToOpen={this.onClickToOpen} />
                            </Tab.Pane>
                            <Tab.Pane fade={true} eventKey="tab_sms">
                                <SMSMarketingOnsite {...this.state} onClickToOpen={this.onClickToOpen} />
                            </Tab.Pane>
                        </Tab.Content>
                    </Tab.Container>
                </Tab.Pane>
            </Tab.Content>
        </Tab.Container>


            <SubmitFeedRequest supportUrl={'#'} videoUrl={'#'} cancelFeed={() => this.props.onBack()} sumbitFeed={() => this.onSubmit()} loading={loading} buttonText={this.state.buttonText} />

            <Suspense fallback={<></>}>
                {showModal ? <ShareEmbedModal shareEmbedCallback={this.shareEmbedCallback} shareEmbedModal={showModal} onClickToShare={this.onClickToShare} isLoading={this.state.isLoading} text="Enter the recipient's email address to share the UGC Uploader code:" heading="Share UGC Uploader URL via email" /> : null}
            </Suspense>
        </>
    }
}
const mapStateToProps = (state) => {
    return {
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getAllfeeds: () => dispatch(getFeedsData()),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(OnsiteUpload)