import React, { Component, Suspense } from 'react';
import { Tab, Nav, OverlayTrigger, Tooltip, Dropdown, InputGroup, DropdownButton, Form } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';
import { EmbedURL, EMBED_MIN } from '../../../Services/Api'
import { mailSendRequest } from '../../../Services/Actions/mailActions';
import ShareEmbedModal from '../PublishCommonComponents/ShareEmbedModal';


class CodeShareCopy extends Component {
    constructor() {
        super();
        this.state = { showModal: false, codeCopied: false, isLoading: false, widthSign: "%", heightSign: "%", width: "100", height: "100", id: "#embedCode_" };
    }
    shareEmbedModal = (id) => event => this.setState({
        showModal: true,
        id: id
    })
    shareEmbedCallback = (shareEmbedEvent) => {
        this.setState({
            showModal: shareEmbedEvent
        })
    }

    codeCopy = (id) => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { codeCopied } = this.state
        const { copyText, id, height, onClickToOpen, readOnly } = this.props

        return <div className="mb-2 mb-xl-3 position-relative">
            <textarea className={`form-control textarea-min-row-${height} fs-10`} id={id} onClick={readOnly ? () => { this.codeCopy(id) } : null} value={copyText} defaultValue={copyText} readOnly={readOnly} />
            <div className="emebed_action position-absolute" style={{bottom:5, right:7}}>
                <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy Code' : 'Code Copied'}</Tooltip>}>
                    <button type="button" className="btn btn-link text-decoration-none text-gray-500 p-0 me-2" onClick={() => { this.codeCopy(id) }}>
                        {!codeCopied ?
                            <i className="ri-file-copy-fill icon-6"></i>
                            :
                            <i className="ri-check-line icon-6" style={{ color: '#6AC259' }}></i>
                        }
                    </button>
                </OverlayTrigger>
                <OverlayTrigger placement="top" overlay={<Tooltip>Share Code</Tooltip>}>
                    <button className="btn btn-link text-decoration-none text-gray-500 p-0 GetShareCode" type="button" onClick={onClickToOpen(copyText)}>
                        <i className="ri-mail-fill icon-6"></i>
                    </button>
                </OverlayTrigger>
            </div>
        </div>

    }
}

export default CodeShareCopy;