import React, { Component } from 'react';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { COPY_TO_CLIPBOARD } from '../../../Services/Utils/utils';
import { HOST_URL, EmbedURL } from '../../../Services/Api';

class UrlInput extends Component {
    constructor(props) {
        super(props);
        this.state = { showModal: false, genrateCode: false, codeCopied: false, shareLink: "", isLoading: false };
    }
    codeCopy = (id) => event => {
        this.setState({
            codeCopied: true
        })
        setTimeout(() => {
            this.setState({
                codeCopied: false
            })
        }, 1500);
        COPY_TO_CLIPBOARD(id);
    }
    render() {
        const { genrateCode, showModal, codeCopied } = this.state
        const { inputUrl, inputId, inputSize, share, copy, edit, onClickToOpen } = this.props
        return <div className="position-relative mb-2">
            <input type={'text'} id={inputId} className={`text-ellipsis form-control ${inputSize ? `form-control-${inputSize}` : ``}`} value={inputUrl} readOnly onClick={this.codeCopy(inputId)} style={{ paddingRight: 70 }} />
            <div className="d-flex align-items-center position-absolute top-50 translate-middle-y end-0 pe-2">
                <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy URL' : 'URL Copied'}</Tooltip>}>
                    <button type="button" className="btn btn-icon btn-xs btn-active-light btn-active-color-primary ms-1" onClick={this.codeCopy(inputId)}>
                    {!codeCopied ?
                        <i className="ri-file-copy-line"></i>
                        :
                        <i className="ri-check-line" style={{ color: '#6AC259' }}></i>
                    }
                    </button>
                </OverlayTrigger>
                <OverlayTrigger placement="top" overlay={<Tooltip>{!codeCopied ? 'Copy URL' : 'URL Copied'}</Tooltip>}>
                    <button type="button" className="btn btn-icon btn-xs btn-active-light btn-active-color-primary ms-1" onClick={onClickToOpen(inputUrl)}>
                        <i className="ri-mail-line"></i>
                    </button>
                </OverlayTrigger>
                {/* <OverlayTrigger placement="top" overlay={<Tooltip>Edit Campaign Url</Tooltip>}>
                            <button type="button" className="btn btn-icon btn-xs btn-active-light btn-active-color-primary ms-1" onClick={this.EditCampaignUrlModal}>
                                <i className="ri-edit-box-line"></i>
                            </button>
                        </OverlayTrigger> */}
            </div>
        </div>
    }
}

export default UrlInput;